	methods: {
		// 请求事件
		isRequest() {
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					uni.showToast({
						icon: 'success',
						title: '请求数据成功'
					})
					resolve()
				}, 1000)
			})
		},
		// 导航栏点击
		navClick(index) {
			this.currentTab = index //设置swiper的第几页
			this.tabClick = index //设置导航点击了哪一个
			this.navIndex = index;
			let _this = this;
			let openId = uni.getStorageSync('openid');

			if (index == 1) {
				//进行中
				uni.request({
					url: _this.$store.state.baseurl + '/order/findOrderMessage',
					data: {
						openId: openId,
						orderStart: 2
					},
