						//_this.$store.commit('setCart',cart);
						//console.log(_this.$store.state.cart)
						//console.log(res.data.data)
						_this.start = res.data.data
					}
				})
			} else if (index == 2) {
				//已完成
				uni.request({
					url: _this.$store.state.baseurl + '/order/findOrderMessage',
					data: {
						openId: openId,
						orderStart: 4
					},
